<template>
  <div id="singleBlog">
    <div v-show="this.isDeleted" class="alert alert-warning alert-dismissible fade show" role="alert">
      <strong>{{this.blog.title ? this.blog.title :'文章'}}</strong> have been deleted successfully.
      <button type="button" class="close" data-dismiss="alert" aria-label="Close">
        <span aria-hidden="true">&times;</span>
      </button>
    </div>
    <div class="card text-center" v-show="!this.isDeleted">
      <div class="card-header">
        Blogs
      </div>
      <div id="blog-container">
        <div class="card-body">
          <h5 class="card-title">{{blog.title}}</h5>
          <p class="card-text">{{blog.content}}</p>
          <button  class="btn btn-primary" @click.prevent="deleteBlog(blog.id)">删除</button>
          <router-link :to="'/editblog/'+blog.id" class="btn btn-primary">编辑</router-link>
        </div>
        <div class="card-footer text-muted">
          author:{{blog.author}} 分类:{{categoriesStr}}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        name: "SingleBlog",
        data(){
          return {
            blog:{},
            isDeleted:false
          }
        },
      computed:{
          categoriesStr(){
            // return this.blog.categories.toString();
            return 'Vue.js';
          }
      },
      methods:{
       deleteBlog(blogid){
         this.$http.delete("https://myblog-49807.firebaseio.com/blogs/"+this.blog.id+".json")
           .then((resp)=>{
             this.isDeleted=true;
           })
           .catch((err)=>{
             console.log(err)
           })
       }
      },
        created(){
          this.blog.id=this.$route.params.id;
          this.$http.get("https://myblog-49807.firebaseio.com/blogs/"+this.blog.id+".json")
            .then((resp)=>{
              // console.log(resp)
              if (resp.body){
                this.blog=resp.body;
                this.blog.id=this.$route.params.id;
              }else{
                this.isDeleted=true;
              }
            })
            .catch((err)=>{
              console.log(err)
            })
        }
    }
</script>

<style scoped>

</style>



