<template>
    <div id="editBlg">
      <h2 class="d-flex justify-content-center">编辑博客</h2>
      <form v-show="!isSubmited">
        <div class="form-group">
          <label for="newblogtitle">文章标题</label>
          <input  class="form-control" id="newblogtitle" placeholder="标题这里添加" v-model="blog.title" required>
        </div>
        <div class="form-group">
          <label for="exampleFormControlTextarea1">Example textarea</label>
          <textarea class="form-control" id="exampleFormControlTextarea1" rows="5" v-model="blog.content"></textarea>
        </div>
        <div id="categories">
          <div class="form-check form-check-inline">
            <input class="form-check-input" type="checkbox"  value="Vue.js" v-model="blog.categories">
            <label class="form-check-label">Vue.js</label>
          </div>
          <div class="form-check form-check-inline">
            <input class="form-check-input" type="checkbox"  value="React.js" v-model="blog.categories">
            <label class="form-check-label">React.js</label>
          </div>
          <div class="form-check form-check-inline">
            <input class="form-check-input" type="checkbox" value="Angular.js" v-model="blog.categories">
            <label class="form-check-label">Angular.js</label>
          </div>
          <div class="form-check form-check-inline">
            <input class="form-check-input" type="checkbox" value="Bootstrap" v-model="blog.categories">
            <label class="form-check-label">Bootstap</label>
          </div>
        </div>
        <div class="form-group">
          <label>作者</label>
          <select class="form-control" v-model="blog.author">
            <option v-for="author in authors">{{author}}</option>
          </select>
        </div>
        <button type="button" @click.prevent="submitBlog" class="btn btn-outline-primary">发布</button>
      </form>
      <hr>
      <div v-show="isSubmited" class="alert alert-primary" role="alert">
        恭喜你，文章"{{blog.title}}"已发布成功!
      </div>
      <div id="preview">
        <h4>文章标题是:{{blog.title}}</h4>
        <p>文章内容</p>
        <p v-text="blog.content"></p>
        <label>文章类别</label>
        <ul>
          <li v-for="category in blog.categories">{{category}}</li>
        </ul>
        <p>文章作者:{{blog.author}}</p>
      </div>
    </div>
</template>

<script>
    export default {
        name: "AddBlog",
        data(){
          return {
            blog:{
              title:'',
              content:'',
              categories:[],
              author:''
            },
            authors:['Ben','Simon','Luke','Oberon'],
            isSubmited:false
          }
        },
      methods:{
          submitBlog(){
            this.$http.put("https://myblog-49807.firebaseio.com/blogs/"+this.blog.id+".json",this.blog)
              .then((resp)=>{
              console.log(resp);
              this.isSubmited=true;
            })
              .catch((err)=>{
                console.log(err);
              })
          }
      },
      created(){
        this.blog.id=this.$route.params.id;
        this.$http.get("https://myblog-49807.firebaseio.com/blogs/"+this.blog.id+".json")
          .then((resp)=>{
            // console.log(resp)
            if (resp.body){
              this.blog=resp.body;
              this.blog.id=this.$route.params.id;
            }
          })
          .catch((err)=>{
            console.log(err)
          })
      }
    }
</script>

<style scoped>

</style>



