<template>
    <div id="showblogs">
      <h2 class="d-flex justify-content-center">文章展示</h2>
      <hr>
      <div class="form-group row">
        <div class="col-sm-12">
          <input  class="form-control" id="search" placeholder="search things..." v-model="search">
        </div>
      </div>
      <div class="card" v-for="blog in filterBlogs">
        <div class="card-body">
          <router-link :to="'/blog/'+blog.id">
            <h4 v-titlerainbow class="card-title">{{blog.title|capitalize}}</h4>
          </router-link>
          <p class="card-text">{{blog.content}}</p>
        </div>
      </div>
    </div>
</template>

<script>
    export default {
        name: "ShowBlogs",
        data(){
          return{
            blogs:[],
            search:''
          }
        },
        created(){
          this.$http.get("https://myblog-49807.firebaseio.com/blogs.json")
            .then((resp)=>{
              return resp.json();
              // this.blogs=resp.body.slice(0,10);
            })
            .then((data)=>{
              for (let key in data){
                let blog={};
                blog=data[key]
                blog.id=key
                this.blogs.push(blog)
              }
            })
            .catch((err)=>{
              console.log(err)
            })
        },
      computed:{
          filterBlogs(){
            return this.blogs.filter((blob)=>{
              return blob.title.match(this.search);
            })
          }
      },
      directives:{
        'blogtheme':{
          bind(el, binding, vnode) {
            let width = binding.value;
            if (binding.arg == 'wide') {
              el.style.width = width;
            }
          }
        }
      },
      filters:{
          capitalize:(value)=>{
            if (!value) return '';
            return value.toString().toUpperCase();
          }
      }
    }
</script>

<style scoped>
a{
  text-decoration: none;
}
</style>



